<!-- 居民协议列表 -->
<template>
  <div class="list-common-box argument-list-box">
    <!-- 标题 -->
    <div class="title-box">
      <Breadcrumb>
        <span slot="custom">{{userName}}</span>
      </Breadcrumb>
    </div>
    <!-- 主要数据 -->
    <div class="card-box">
      <!-- 搜索 -->
      <div class="search-bar">
        <!-- 左 -->
        <div class="search-left">
          <div class="search-item">
            <span class="search-label">协议标题</span>
            <el-input v-model.trim="searchKey.agreementName" placeholder="请输入协议标题" clearable></el-input>
          </div>
          <div class="search-item">
            <span class="search-label">协议类型</span>
            <el-select placeholder="请选择协议类型" v-model="searchKey.agreementType" clearable>
              <el-option v-for="item in agreementTypeOption" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </div>
        </div>
        <div class="search-right">
          <el-button type="primary" plain icon="el-icon-search" @click="querylist()">查询</el-button>
          <el-button type="primary" plain icon="el-icon-delete" @click="Refreshlist()">清空</el-button>
        </div>
      </div>
      <!-- 表格 -->
      <div class="table-box">
        <el-table :data="dataList" tooltip-effect="dark" stripe height="100%"> 
          <el-table-column label="序号" align="center" width="80">
            <template slot-scope="scope">
              {{scope.$index+(pageNum - 1) * pageSize + 1}}
            </template>
          </el-table-column>
          <el-table-column prop="agreementName" label="协议标题" align="center">
            <template slot-scope="scope">
              {{scope.row.agreementName?scope.row.agreementName:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="agreementTypeStr" label="协议类型" align="center">
            <template slot-scope="scope">
              {{scope.row.agreementTypeStr?scope.row.agreementTypeStr:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="registerOrganization" label="创建机构" align="center">
            <template slot-scope="scope">
              {{scope.row.registerOrganization?scope.row.registerOrganization:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="teamIsFirstStr" label="甲方或乙方" align="center">
            <template slot-scope="scope">
              {{scope.row.teamIsFirstStr?scope.row.teamIsFirstStr:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="createDateStr" label="签约时间"  align="center" width="170">
            <template slot-scope="scope">
              {{scope.row.createDateStr?scope.row.createDateStr:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="registerUser" label="签约医生"  align="center">
            <template slot-scope="scope">
              {{scope.row.registerUser?scope.row.registerUser:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column label="操作"  align="center" width="170">
            <template slot-scope="scope">
              <el-button type="primary" @click="handleView(scope.row)" class="green">查看详情</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!-- 分页 -->
      <div class="page-box">
         <Paginaction class='pagination' :goPage="pageNum" :totalNum="totalNum" @updateList="changePage" ref="paginationEvent"/>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'agreement-list',
    data() {
      return {
        userName:this.$route.query.name,
        searchKey:{
          agreementName:'',//协议名称
          agreementType:''//协议类型
        },
        agreementTypeOption:[
          {
            value: 0,
            label: '全部'
          },
          {
            value: 1,
            label: '签约协议'
          },
          {
            value: 2,
            label: '解约协议'
          }
        ],
        pageNum:1,//当前页
        pageSize:10,//每页条数
        totalNum: 0, //总条数
        dataList: [] //居民协议列表
      }
    },
    created() {
      this.getDatalist()
    },
    methods: {
      //查看详情
      handleView(scope) {
        this.$router.push({
          path: '/resident-manage/family-archives/agreement-detail?id=' + scope.agreementId
        })
      },
      //查询
      querylist() {
        this.pageNum = 1
        this.getDatalist()
      },
      //重置
      Refreshlist() {
        this.pageNum = 1
        this.pageSize = 10
        Object.keys(this.searchKey).forEach(k => this.searchKey[k] = '')
        this.getDatalist()
      },
      //分页改变
      changePage(query){
        this.pageNum = query.pageNum
        this.pageSize = query.pageSize
        this.getDatalist()
      },
      //获取居民协议列表
      getDatalist(query) {
        this.$emit("viewLoad", true);
        let params = {}
        params.pageNum  = this.pageNum
        params.pageSize = this.pageSize
        params.recordId = this.$route.query.id
        params = Object.assign({},params,this.searchKey)
        this.$http.post(process.env.VUE_APP_API + '/family/personAgreement/getDtoByPage', params).then(res => {
          if (res.code===200) {
            this.dataList = res.data.list || [] //居民协议列表
            this.totalNum = res.data.total //总条数
            this.pageNum = res.data.pageNum //当前页
            this.pageSize = res.data.pageSize //每页条数
          } else {
            this.$message.error(res.msg);
          }
          this.$emit("viewLoad", false);
        })
        .catch(error => {
          this.$emit("viewLoad", false);
          this.$message.error(error);
        });
      }
    }
  }
</script>
<style lang='less' scoped>
</style>